import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/full_stack_exercise_frontend/dist/',
    name: 'Home',
    component: Home
  },
  {
    path: '/full_stack_exercise_frontend/dist/Books',
    name: 'Books',
    component: () =>
      import('../views/Books.vue')
  },
  {
    path: '/full_stack_exercise_frontend/dist/Movies',
    name: 'Movies',
    component: () =>
      import('../views/Movies.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
